@charset "UTF-8";
@import url("https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@400;700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Roboto&display=swap");
/* =======================================
	Common Style
======================================= */
html * {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-text-size-adjust: 100%;
}

html {
  overflow: auto;
  height: 100%;
}

html,
body {
  position: relative;
  -webkit-overflow-scrolling: touch;
  color: #43425d;
  font-size: 16px;
  font-family: "Source Sans Pro", "Helvetica Neue", Arial, "Hiragino Kaku Gothic ProN", "Hiragino Sans", Meiryo, sans-serif;
  line-height: 1.6rem;
}

body {
  right: 0;
  left: 0;
  margin: auto;
  max-width: 980px;
  background: #effaff;
  color: #43425d;
}

*:focus {
  outline: none;
}

* {
  -webkit-appearance: none;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0.1);
}

a {
  color: #43425d;
  text-decoration: none;
}

h1 {
  margin: 0 auto 1.5rem;
  color: #11b1dc;
  text-align: center;
  font-weight: bold;
  font-size: 24px;
}

h1 img {
  height: 3rem;
}

.text-medium {
  margin: 0 auto 1.5rem;
  font-size: 16px;
  line-height: 26px;
}

.text-small {
  margin: 0 auto 1.5rem;
  font-size: 14px;
  line-height: 22px;
}

.center {
  text-align: center;
}

.small {
  font-size: 13px;
}

.bold {
  font-weight: bold;
}

.container {
  margin: 3rem auto 0;
  max-width: 768px;
  width: 100%;
}

.card {
  margin: auto;
  padding: 2rem 1rem .5rem;
  width: calc(100% - 4rem);
  border-radius: 10px;
}

.card.white {
  background: #fff;
  box-shadow: 0 -3px 20px rgba(195, 232, 247, 0.7);
}

.list-presents {
  margin: 0 auto 1.5rem;
  width: 100%;
}

.list-presents > dt {
  position: relative;
  padding: .5rem 1.5rem;
  width: 80%;
  border-radius: 0 2rem 2rem 0;
  background: linear-gradient(to left, #11b1dc 0%, #67dfff 100%);
  color: #fff;
  font-weight: bold;
  font-size: 24px;
  font-family: "Roboto", sans-serif;
  cursor: pointer;
}

.list-presents > dt::before {
  position: absolute;
  right: 20px;
  display: inline-block;
  width: 25px;
  content: "＋";
  text-align: center;
}

.list-presents > dt.open::before {
  content: "ー";
}

.list-presents > dd {
  position: relative;
  visibility: hidden;
  overflow: hidden;
  width: 100%;
  animation: 600ms ease-in both fadeout;
}

.list-presents > dd div {
  visibility: hidden;
  height: 0;
  opacity: 0;
  transition: all 300ms ease-out;
}

.list-presents > dd.open {
  display: block;
  height: auto;
  animation: 600ms ease-out both fadein;
}

.list-presents > dd.open div {
  visibility: visible;
  height: 100%;
  opacity: 1;
  transition: all 300ms ease-out;
}

.list-presents > dd dl:last-of-type {
  margin: 0 auto;
}

@keyframes fadein {
  0% {
    visibility: hidden;
    padding: 0 1rem 0 3rem;
    opacity: 0;
  }
  100% {
    visibility: visible;
    padding: 1.5rem 1rem 0 3rem;
    opacity: 1;
  }
}

@keyframes fadeout {
  0% {
    visibility: visible;
    padding: 1.5rem 1rem 0 3rem;
    opacity: 1;
  }
  100% {
    visibility: hidden;
    padding: 0 1rem 0 3rem;
    opacity: 0;
  }
}

.card-gift {
  position: relative;
  margin: 0 auto 1.5rem;
  padding: 1rem;
  min-height: 115px;
  width: 100%;
  border-radius: 4px;
  background: #fff;
  box-shadow: 3px 3px 10px rgba(195, 232, 247, 0.7);
}

.card-gift::before {
  position: absolute;
  top: 0;
  bottom: 0;
  left: -1.5rem;
  margin: auto;
  width: 1.5rem;
  height: 2px;
  background: rgba(17, 177, 220, 0.6);
  content: "";
}

.card-gift::after {
  position: absolute;
  top: calc( 100% - 150% - 1.5rem);
  left: -1.5rem;
  margin: auto;
  width: 2px;
  height: calc(100% + 1rem);
  background: rgba(17, 177, 220, 0.6);
  content: "";
}

.card-gift .item-area {
  position: absolute;
  top: 1rem;
  text-align: center;
}

.card-gift .item-area .date {
  display: block;
  margin: 0 auto .5rem;
  color: #11b1dc;
  font-weight: bold;
  font-size: 18px;
}

.card-gift .item-area figure {
  overflow: hidden;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background: linear-gradient(to left, #11b1dc 0%, #67dfff 100%);
  box-shadow: 3px 3px 10px rgba(17, 177, 220, 0.2);
  text-align: center;
}

.card-gift .item-area figure img {
  width: 100%;
  height: auto;
}

.card-gift .item-area a {
  display: block;
  transition: opacity 300ms ease-out;
}

.card-gift .item-area a:hover {
  opacity: .8;
}

.card-gift.give {
  padding-left: 5rem;
}

.card-gift.give .item-area {
  left: 1rem;
}

.card-gift.take {
  padding-right: 5rem;
}

.card-gift.take .item-area {
  right: 1.5rem;
}

.card-gift > dt {
  font-weight: bold;
  font-size: 16px;
}

.card-gift > dt::before {
  position: absolute;
  top: 0;
  bottom: 0;
  left: -2.1rem;
  z-index: 10;
  display: block;
  margin: auto;
  width: 20px;
  height: 20px;
  border: 4px solid #11b1dc;
  border-radius: 50%;
  background: #effaff;
  content: "";
}

hr {
  margin: 0 auto 1.5rem;
  border: none;
}

hr.dot_line {
  border: 1px dashed rgba(17, 177, 220, 0.2);
}

/* =======================================
	Components
======================================= */
.btn {
  position: relative;
  z-index: 0;
  display: block;
  margin: 0 auto 1.5rem;
  max-width: 248px;
  width: 100%;
  height: 48px;
  border-radius: 25px;
  color: #fff;
  text-align: center;
  font-weight: bold;
  font-size: 1.1rem;
  line-height: 48px;
}

.btn::before, .btn::after {
  position: absolute;
  left: 0;
  z-index: -1;
  width: 100%;
  height: 100%;
  border-radius: 25px;
  content: "";
  transition: 300ms ease-out;
}

.btn:hover::before {
  opacity: 0;
}

.btn:active::after {
  opacity: 0;
}

.btn-primary {
  background: #11b1dc;
}

.btn-primary::before {
  background: linear-gradient(to left, #11b1dc 0%, #67dfff 100%);
}

.btn-primary::after {
  box-shadow: 3px 3px 10px rgba(12, 147, 201, 0.6);
}

.btn-primary:active {
  box-shadow: 1px 1px 10px #0c93c9 inset;
}

.btn-info {
  background: #fe719d;
}

.btn-info::before {
  background: linear-gradient(to left, #fe719d 0%, #ffc6d8 100%);
}

.btn-info::after {
  box-shadow: 3px 3px 10px rgba(175, 55, 129, 0.6);
}

.btn-info:active {
  box-shadow: 1px 1px 10px #af3781 inset;
}

.link-text {
  position: relative;
  display: inline-block;
  overflow: hidden;
  margin: 0 auto 1.5rem;
  line-height: 1.2rem;
}

.link-text.forget {
  padding: 0 0 0 1.7rem;
}

.link-text.forget::before {
  position: absolute;
  top: 0;
  bottom: 0;
  left: .2rem;
  display: inline-block;
  margin: auto;
  width: 1.2rem;
  height: 1.2rem;
  border-radius: 50%;
  background: #fe719d;
  color: #fff;
  content: "？";
  text-align: center;
  line-height: 1.2rem;
}

.link-text.back::before {
  display: inline-block;
  padding: 0 .3rem 0 0;
  color: #11b1dc;
}

.form-group {
  margin: 0 auto 1.5rem;
  text-align: left;
}

.form-group span {
  display: block;
  font-size: 13px;
}

.form-group input {
  padding: 1rem .5rem;
  width: 100%;
  height: 48px;
  border: 1px solid #d1d1d1;
  border-radius: 4px;
  font-size: 1rem;
  line-height: 1rem;
}

.form-group input::placeholder {
  padding: .1rem 0 0 0;
  color: #cecfd0;
  font-size: 14px;
}

.form-group input:focus {
  border: 1px solid #a7a7a7;
  transition: 300ms ease-out;
}

.spinner {
  display: block;
  margin: 4rem auto;
  width: 3rem;
  height: 3rem;
  border: 3px solid transparent;
  border-top-color: rgba(17, 177, 220, 0.5);
  border-bottom-color: rgba(17, 177, 220, 0.5);
  border-radius: 50%;
  animation: rotation .8s ease infinite;
}

@keyframes rotation {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.badge {
  display: inline-block;
  padding: .4em .6em;
  border-radius: 10rem;
  background: #11b1dc;
  color: #fff;
  vertical-align: baseline;
  text-align: center;
  white-space: nowrap;
  font-weight: 700;
  font-size: 75%;
  line-height: 1;
}

.fas::before {
  padding-right: .5rem;
}

#search {
  position: absolute;
  top: 0;
  right: 0;
  display: block;
  overflow: hidden;
  width: 80px;
  height: 80px;
  font-size: 0;
  pointer-events: auto;
}

#search::before {
  position: absolute;
  z-index: 1;
  display: block;
  padding: 0;
  width: 90px;
  color: #fff;
  text-align: center;
  font-size: 30px;
  line-height: 70px;
}

#search::after {
  position: absolute;
  bottom: 0;
  left: 0;
  z-index: 0;
  display: block;
  padding: 0;
  width: 100px;
  height: 100px;
  border-radius: 50px;
  background: #11b1dc;
  color: #fff;
  content: "";
  text-align: center;
  font-size: 30px;
  line-height: 50px;
  transition: all 300ms ease-out;
}

#search.show::before {
  content: "✕";
}

#search:hover::after {
  background: #0c93c9;
}

.search-wrap {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 99;
  display: block;
  overflow: hidden;
  padding: 50px 0 0;
  width: 100%;
  height: 100vh;
  pointer-events: none;
}

.search-wrap::after {
  position: absolute;
  top: -20px;
  right: -20px;
  z-index: 0;
  display: block;
  border-radius: 100%;
  background: #11b1dc;
  content: "";
  pointer-events: auto;
}

.search-wrap.show::after {
  animation: 600ms ease-out both 500ms search;
}

.search-wrap dl {
  position: relative;
  z-index: 1;
  margin: 50px auto 0;
  width: 80%;
  opacity: 0;
}

.search-wrap dl dt {
  padding: 0 0 .5rem;
  color: #fff;
  font-size: 1.2rem;
}

.search-wrap dl dd {
  padding: .7rem 1rem;
  border-radius: 10px;
  background: #fff;
}

.search-wrap dl dd span {
  margin: 0 .1rem .2rem;
}

.search-wrap .show dl {
  animation: 300ms ease-out both 1100ms fadein;
}

@keyframes search {
  0% {
    top: -20px;
    right: -20px;
    width: 100px;
    height: 100px;
    opacity: .5;
  }
  100% {
    top: -500px;
    right: -500px;
    width: 2000px;
    height: 2000px;
    opacity: 1;
  }
}
